import { GetServerSideProps, NextPage } from 'next';
import axios from 'axios';
import { Movie, MoviesResponse } from '../types/movie';

interface HomePageProps {
  movies: Movie[];
}

const HomePage: NextPage<HomePageProps> = ({ movies }) => {
  return (
    <div className="relative bg-black">
      {/* Hero Section */}
      <div className="relative w-full h-[500px]">
        {movies.length > 0 && (
          <img
            className="object-cover w-full h-full"
            src={`https://image.tmdb.org/t/p/original${movies[0].backdrop_path}`}
            alt="Banner Image"
          />
        )}
        <div className="absolute inset-0 bg-black opacity-50"></div>
        <div className="absolute inset-0 flex flex-col items-center justify-center text-white p-5">
          <h1 className="text-4xl font-bold">{movies[0]?.title}</h1>
          <p className="text-lg mt-3">{movies[0]?.overview}</p>
        </div>
      </div>

      {/* Movies List Section */}
      <div className="p-5 grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 gap-5 mt-10">
        {movies.map((movie) => (
          <div key={movie.id} className="relative">
            <img
              className="w-full h-auto rounded-md shadow-lg"
              src={`https://image.tmdb.org/t/p/w500${movie.poster_path}`}
              alt={movie.title}
            />
            <div className="absolute top-0 left-0 bg-black opacity-40 w-full h-full rounded-md"></div>
            <div className="absolute bottom-5 left-5 text-white font-bold">
              {movie.title}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export const getServerSideProps: GetServerSideProps = async () => {
  const API_KEY = 'eyJhbGciOiJIUzI1NiJ9.eyJhdWQiOiIxY2ViYTE1YzhlOTMwNmExNGMxZWQ3ZDUyYTRlNGFhMCIsIm5iZiI6MTczMjYxMjEwNC4xMTAzNDA0LCJzdWIiOiI2NzQ1OGRkMzgwYjQ0YTg5MzdiN2MzNDUiLCJzY29wZXMiOlsiYXBpX3JlYWQiXSwidmVyc2lvbiI6MX0.f5pYhZOw9kt_ZFyPzWay-D1seZ2dOGJ43W7Mb5-a-A0';
  const url = `https://api.themoviedb.org/3/discover/movie`;

  try {
    const response = await axios.get<MoviesResponse>(url, {
      headers: {
        Authorization: `Bearer ${API_KEY}`,
      },
    });

    return {
      props: {
        movies: response.data.results || [],
      },
    };
  } catch (error) {
    console.error('Error fetching movies:', error);
    return {
      props: {
        movies: [],
      },
    };
  }
};

export default HomePage;
